<!--公用组件：
 /**
  * @desc index.vue
  * @module @/components
  * @desc 组件描述：修改密码
  * @author zkhq
  * @date 2020/3/30 10:09
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <el-dialog
    title="修改密码"
    :visible.sync="dialogVisible"
    width="480px"
  >
    <el-form :model="ruleForm" :rules="rules" label-suffix=":" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <el-form-item label="旧密码" prop="passwordOld">
        <el-input type="password" v-model="ruleForm.passwordOld" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="passwordNew">
        <el-badge :hidden="badgeHidden" :value="badgeValue" :type="badgeInfo">
          <el-input type="password" v-model="ruleForm.passwordNew" placeholder=""></el-input>
        </el-badge>
      </el-form-item>
      <el-form-item label="确认密码" prop="passwordAgain">
        <el-input type="password" v-model="ruleForm.passwordAgain" placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="close"  >取 消</el-button>
        <el-button type="primary" @click="save" >确 定</el-button>
      </span>
  </el-dialog>
</template>

<script>
// import
export default {
  name: 'PasswordChange',
  data () {
    let checkPasswordAgain = (rule, value, callback) => {
      if (value !== this.ruleForm.passwordNew) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 编辑弹框显示隐藏
      dialogVisible: false,
      // 表单内容
      ruleForm: {
        passwordOld: '',
        passwordNew: '',
        passwordAgain: ''
      },
      // 表单规则
      rules: {
        passwordOld: [
          {required: true, message: '请输入旧密码', trigger: 'blur'}
        ],
        passwordNew: [
          {required: true, message: '请输入新密码', trigger: 'blur'},
          {min: 6, message: '最小长度为 6', trigger: 'blur'}
        ],
        passwordAgain: [
          {required: true, message: '请再次输入新密码', trigger: 'blur'},
          {validator: checkPasswordAgain, trigger: 'blur'}
        ]
      }
    }
  },
  props: {
    // visible: {
    //   default: false,
    //   type: Boolean
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {
    passwordStrong () {
      return this.$common.analyzePasswordSecurityLevel(this.ruleForm.passwordNew)
    },
    badgeHidden () {
      return this.$common.objC(this.ruleForm, 'passwordNew').length < 6
    },
    badgeValue () {
      let dict = {
        0: '',
        1: '弱',
        2: '中',
        3: '强',
        4: '最高'
      }
      return `密码等级${dict[this.passwordStrong]}`
    },
    badgeInfo () {
      let dict = {
        0: 'info',
        1: 'danger',
        2: 'warning',
        3: 'primary',
        4: 'success'
      }
      return dict[this.passwordStrong]
    }
  },
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 打开弹窗
     */
    open () {
      this.dialogVisible = true
    },
    close () {
      this.dialogVisible = false
    },
    save () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.change()
        } else {
          return false
        }
      })
    },
    change () {
      this.$api.modifyPwd({
        newpwd: this.ruleForm.passwordNew,
        oldpwd: this.ruleForm.passwordOld
      }).then((respone) => {
        if (respone.data.code === 0) {
          this.$message({
            message: '密码修改成功',
            type: 'success'
          })
          this.close()
        } else {
          this.$message.error(respone.data.message)
        }
      }).catch((error) => {
        console.log(`%c${error} 修改密码失败！！`, 'color:blue')
        this.$message.error(`修改密码失败！！`)
      })
    }
  }
}
</script>

<style scoped>
</style>



