import {objC} from '@/utils/common'

const state = {
  routers: [],
  addRouters: [],
  idToRouter: {}
}
const mutations = {
  SET_ROUTERS: (state, routers) => {
    state.addRouters = routers
    state.routers = state.routers.concat(routers)
    getIdToRouter(routers, state.idToRouter)
  },
  changeRoute: (state, {id, obj}) => {
    let router = state.idToRouter[id]
    router.hidden = obj.hidden
  }
}
const actions = {
  GenerateRoutes ({commit}, asyncRouter) {
    commit('SET_ROUTERS', asyncRouter)
  }
}

function getIdToRouter (routers, idToRouter = {}) {
  for (let i = 0, l = routers.length; i < l; i++) {
    let item = routers[i]
    idToRouter[item.name] = item
    if (objC(item, 'children', 'length')) {
      getIdToRouter(item.children, idToRouter)
    }
  }
  return idToRouter
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



