import Vue from 'vue'
import store from '@/store'
import {getMenuId} from '@/utils/common'

Vue.directive('buttonshow', {
  inserted (el, binding, vnode) {
    let component = binding.value
    let menu = getMenuId()
    if (!getComponentByMenu(menu, component)) {
      el.parentNode && el.parentNode.removeChild(el)
    }
  }
})

/**
 * @desc 判断当前菜单下的按钮是否有权限
 * @param menu 菜单 id
 * @param component 按钮 component
 */
function getComponentByMenu (menu, component) {
  if (!(menu && component)) return false

  let blo = store.getters['button/hasButtom']({menu, component})

  if (blo) return blo

  if (store.getters['menu/menuIdToTop'](menu)) {
    return blo
  }

  let pid = store.getters['menu/menuIdToPid'](menu)
  return getComponentByMenu(pid, component)
}



