const state = {
  // 污染物数组
  arr: [
    'aqi',
    'pm25',
    'pm10',
    'co',
    'no2',
    'so2',
    'o3',
    'o3_8h'
  ],
  // pollToShow: {
  //   aqi: 'AQI',
  //   pm25: 'PM2.5',
  //   pm10: 'PM10',
  //   co: 'CO',
  //   no2: 'NO2',
  //   so2: 'SO2',
  //   o3: 'O3',
  //   o3_8h: 'O3_8h'
  // },
  // 污染物 =》 显示
  pollToShow: {
    aqi: 'AQI',
    pm25: 'PM₂.₅',
    pm10: 'PM₁₀',
    so2: 'SO₂',
    o3: 'O₃',
    o3_8h: 'O₃_8h',
    o38h: 'O₃8h',
    co: 'CO',
    no2: 'NO₂',
    nox: 'NOₓ',
    tvoc: 'TVOC',
    bc: 'BC',
    oc: 'OC',
    voc: 'VOCs',
    nh3: 'NH₃'
  },
  // 污染物 =》 单位
  pollToUnit: {
    aqi: 'μg/m³',
    pm25: 'μg/m³',
    pm10: 'μg/m³',
    co: 'mg/m³',
    no2: 'μg/m³',
    so2: 'μg/m³',
    o3: 'μg/m³',
    o3_8h: 'μg/m³'
  }
}

const getters = {
  // /**
  //  * @desc 通过污染物获取显示用的污染物
  //  * @param {String} poll 污染物
  //  * @returns {String} 显示用的污染物
  //  */
  getShowByPoll: state => poll => state.pollToShow[poll],
  // /**
  //  * @desc 通过污染物获取单位
  //  * @param {String} poll 污染物
  //  * @returns {String} 单位
  //  */
  getUnitByPoll: state => poll => state.pollToUnit[poll]
}

export default {
  namespaced: true,
  state,
  getters
}



