<!--公用组件：
 /**
  * @desc AntIcons
  * @module @/components
  * @desc 组件描述：
  * @author zkhq
  * @date 2020/3/24 16:06
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <components :is="iconName" class="ant-icon"></components>
</template>

<script>
// import
import * as antIcons from '@ant-design/icons-vue/es'

export default {
  name: 'AntIcons',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
    iconClass: {
      type: String,
      required: true
    }
  },
  components: {
    ...antIcons
  },
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {
    iconName () {
      if (antIcons[this.iconClass]) {
        return this.iconClass
      } else {
        return 'span'
      }
    }
  },
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
  }
}
</script>

<style scoped>
  .ant-icon{
    /*cursor: pointer;*/
    font-size: inherit;
    vertical-align: inherit;
    /*line-height: inherit;*/
  }
</style>



