import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import {clearLoginInfo, objC, deepClone, urlPushByRouteName, isArray, getMenuId, setLoginInfo, urlPushById, getLoginInfo} from '@/utils/common'
import {querySystemMenus} from '@/api'
import {rootDirectory, moduleName} from '@/utils/rootDirectory'
import layout from '@/components/layout/index'

Vue.use(VueRouter)

// data 。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。

// 已添加的路由字典
const hasAddRoute = {
  login: true
}

// business 。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。

// 开发环境不使用懒加载, 因为懒加载页面太多的话会造成webpack热更新太慢, 所以只有生产环境使用懒加载
// process.env.NODE_ENV webpack 构建工具提供的参数，表示是否使用生产模式
// require() 是同步加载，应用于 development
// import() 是异步加载，应用于 production
const _import = require('./import-' + process.env.NODE_ENV)

// 全局路由(无需嵌套上左右整体布局)
const globalRoutes = [
  {
    name: 'error',
    path: '/404',
    component: _import('/404'),
    meta: {title: '404未找到'}
  },
  {
    name: 'login',
    path: '/Login',
    // component: _import('/base/Login'),
    component: _import('/Login'),
    meta: {title: '登录'}
  },
  {
    name: 'Test',
    path: '/Test',
    // component: _import('/base/Login'),
    component: _import('/Test'),
    meta: {title: 'Test'}
  }
]

// 生成路由
const router = new VueRouter({
  mode: 'history',
  base: `${rootDirectory}/`,
  scrollBehavior: () => ({y: 0}),
  isAddDynamicMenuRoutes: false, // 是否已经添加动态(菜单)路由
  routes: globalRoutes
})

// 导航守卫控制
router.beforeEach((to, from, next) => {
  // 修改每个页面的 title
  if (to.meta.title) {
    // document.title = to.meta.title + ' - ' + Config.title
    document.title = to.meta.title
  }
  // 添加动态(菜单)路由
  // 1. 全局路由，在获取到 login 后，直接访问
  // 2. 在有 login 的情况下跳转 login，不做阻拦
  // 3. 已经添加路由，验证token
  // 4. 获取菜单列表, 添加并保存本地存储
  if (fnCurrentRouteType(to, globalRoutes) === 'global' && hasAddRoute['login']) {
    next()
  } else if (to.name === 'login' && hasAddRoute['login']) {
    next()
  } else if (router.options.isAddDynamicMenuRoutes) {
    // token 校验
    tokenVerification(to, from, next, function () {
      // 保存当前菜单
      saveCurrentMenu(to)
      next()
    })
  } else {
    // 动态路由
    routerSet(to, from, next)
  }
})

// method 。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。

/**
 * @desc 判断当前路由类型
 * @param {Object} route 当前路由
 * @param {Array} globalRoutes 全局路由
 * @returns {string|*} global: 全局路由, main: 主入口路由
 */
function fnCurrentRouteType (route, globalRoutes = []) {
  let temp = []
  for (let i = 0; i < globalRoutes.length; i++) {
    if (route.path === globalRoutes[i].path) {
      return 'global'
    } else if (globalRoutes[i].children && globalRoutes[i].children.length >= 1) {
      temp = temp.concat(globalRoutes[i].children)
    }
  }
  return temp.length >= 1 ? fnCurrentRouteType(route, temp) : 'main'
}

/**
 * @desc 获取并设置动态路由
 * @param {Object} to 跳转至的页面
 * @param {Object} from 跳转前的页面
 * @param {Function} next 跳转页面的函数
 */
function routerSet (to, from, next) {
  querySystemMenus().then(({data}) => {
    // 保存按钮权限，并返回菜单
    let arr = saveButtonsAndGetMenus(data)
    let code = data.code
    data = {arr}
    menuInit(data)
    // 生成树结构菜单
    let menuArr = data.arr
    // 生成树结构菜单
    let menuTree = getMenuTree(menuArr)
    // 默认菜单
    let mainMenu = data.redirect[data.main]
    // 当前模块默认菜单
    let mainModuleMenu = data.redirect[moduleName]
    // 保存菜单数据
    store.commit('menu/menuArrSave', {arr: deepClone(menuArr)})
    store.commit('menu/menuTreeSave', {arr: deepClone(menuTree)})
    store.commit('menu/mainMenuSave', {num: mainMenu})
    store.commit('menu/mainModuleMenuSave', {num: mainModuleMenu})
    // 生成菜单字典
    let menuIdToMenu = store.state.menu.menuIdToMenu
    // 添加菜单
    fnAddDynamicMenuRoutes(menuTree, menuIdToMenu, mainMenu, mainModuleMenu, () => {
      if (code === 0) {
        // 保存菜单添加记录
        router.options.isAddDynamicMenuRoutes = true
        // 添加完所有路由后，添加保底路由，设置跳转至 404 页面
        router.addRoutes([
          {path: '*', redirect: {name: 'error'}}
        ])
        let {currentMenu} = getLoginInfo()
        if (currentMenu) {
          urlPushById(currentMenu)
        } else {
          next({...to, replace: true})
        }
      } else {
        // 没有 %c 颜色就不会起作用
        console.log(`%c${data.msg} 请求菜单列表和权限失败，跳转至登录页！！`, 'color:blue')
        urlPushByRouteName('login')
      }
    })
  }).catch((e) => {
    // 没有 %c 颜色就不会起作用
    console.log(e)
    router.push({name: 'error'})
  })
}

/**
 * @desc 添加动态(菜单)路由
 * @param {Array} menuTree 树形菜单
 * @param {Object} menuIdToMenu 菜单字典
 * @param {Number} mainMenu 主页面
 * @param {Number} mainModuleMenu 主模块
 */
function fnAddDynamicMenuRoutes (menuTree = [], menuIdToMenu = {}, mainMenu = 1, mainModuleMenu = 1, next) {
  // 路由数据
  let routes = []
  // 默认路由
  // 按模块打包的状态下，进入当前模块默认页面
  if (process.env.NODE_ENV !== 'development' && moduleName) {
    mainMenu = mainModuleMenu
  }
  let mainUrl = objC(menuIdToMenu[mainMenu], 'url')
  if (mainUrl && !hasAddRoute['main']) {
    let route = {
      path: '/',
      name: 'main',
      redirect: mainUrl
    }
    routes.push(route)
    hasAddRoute['main'] = true
  }
  // 添加树形菜单
  routeAdd(routes, menuTree, menuIdToMenu)
  // 菜单显示数据
  let menus = getMenus(routes)
  // 存储路由
  store.dispatch('permission/GenerateRoutes', menus).then(() => {
    // 动态路由添加至 router
    router.addRoutes([
      ...routes
    ])
    if (typeof next === 'function') {
      next()
    }
  })
}

function getMenus (routes) {
  let menus = []
  if (!isArray(routes)) {
    return menus
  }
  for (let i = 0, l = routes.length; i < l; i++) {
    let item = {...routes[i]}
    item.children = getMenus(item.children)
    if (item.hidden) {
      menus = menus.concat(item.children)
    } else {
      menus.push(item)
    }
  }
  return menus
}

/**
 * @desc 添加树形菜单
 * @param {Array} arr 菜单数组
 * @param {Array} tree 树形菜单
 * @param {Object} menuIdToMenu 菜单字典
 */
function routeAdd (arr, tree, menuIdToMenu = {}) {
  for (let i = 0, menu; i < tree.length; i++) {
    menu = tree[i]
    // 已添加过的路由不再添加
    if (hasAddRoute[menu.routeName]) {
      continue
    }
    let route = {
      path: menu.url,
      component: null,
      name: menu.routeName,
      hidden: menu.hidden,
      meta: {
        title: menu.name,
        icon: menu.icon,
        breadcrumb: !menu.hidden
      }
    }
    // 按模块打包状态下，不能添加别的模块的路由
    if (process.env.NODE_ENV === 'development' || !moduleName || (moduleName && menu.module === moduleName)) {
      // 重定向
      if (menu.redirect && objC(menuIdToMenu[menu.redirect], 'url')) {
        route.redirect = menuIdToMenu[menu.redirect].url
      }
      // 添加组件
      if (menu.layout) {
        route['component'] = layout
      } else {
        try {
          let module = menu.module ? '/' + menu.module : ''
          route['component'] = _import(module + menu.url) || null
        } catch (e) {
        }
      }
    }

    arr.push(route)
    hasAddRoute[menu.routeName] = true
    //  添加子项
    if (menu.data && menu.data.length) {
      route.children = []
      routeAdd(route.children, menu.data, menuIdToMenu)
    }
  }
}

/**
 * @desc token 验证
 * @param {Object} to 跳转至的页面
 * @param {Object} from 跳转前的页面
 * @param {Function} next 跳转页面的函数
 * @param {Function} keepOn 验证成功后，执行的回调函数
 */
function tokenVerification (to, from, next, keepOn) {
  // let token = Vue.cookie.get('token')
  let token = sessionStorage.token
  if (!token || !/\S/.test(token)) {
    clearLoginInfo()
    router.options.isAddDynamicMenuRoutes = false
    urlPushByRouteName('login')
  }
  keepOn()
}

/**
 * @desc 保存当前菜单 id 至 loginInfo
 * @param path
 */
function saveCurrentMenu ({path = ''} = {}) {
  let currentMenu = getMenuId({path})
  setLoginInfo({currentMenu})
}

/**
 * @desc 保存按钮权限，并返回菜单
 * @param data
 */
function saveButtonsAndGetMenus ({data}) {
  if (!isArray(data)) {
    return []
  }
  // type 为 3 的是按钮
  let buttons = data.filter(obj => obj.type === '3')
  // 保存按钮
  store.commit('button/arrSave', {arr: buttons})
  // type 为 2 的是菜单
  let menus = data.filter(obj => obj.type === '2' || obj.type === '1')

  return menus
}

/**
 * @desc 根据菜单数据，生成主模块和模块主页面
 * @param data 菜单数据
 */
function menuInit (data) {
  let arr = data.arr
  arr.sort((a, b) => a.orders - b.orders)
  let moduleArr = []
  let mainDict = {}
  // 模块的主页面是否为 layout
  let layoutDict = {}
  data.redirect = {}
  for (let i = 0, l = arr.length; i < l; i++) {
    let {module, mainModule, id, main, layout} = arr[i]
    // 默认第一个
    if (module && !data.redirect[module]) {
      moduleArr.push(module)
      data.redirect[module] = id
      layoutDict[module] = layout
    // 默认第一个不是 layer，不能覆盖 main
    } else if (module && !mainDict[module] && data.redirect[module] && layoutDict[module] && !layout) {
      data.redirect[module] = id
      layoutDict[module] = layout
    }
    // 默认第一个 main
    if (module && main && !mainDict[module]) {
      mainDict[module] = true
      data.redirect[module] = id
    }
    // 默认第一个模块
    if (module && mainModule && !data.main) {
      data.main = module
    }
  }

  if (moduleArr.length) {
    if (!data.main) {
      data.main = moduleArr[0]
    }
  } else {
    let module = 'main'
    data.main = module
    for (let i = 0, l = arr.length; i < l; i++) {
      let {id, main, layout} = arr[i]
      if (!data.redirect[module]) {
        data.redirect[module] = id
        layoutDict[module] = layout
      } else if (!mainDict[module] && layoutDict[module] && !layout) {
        data.redirect[module] = id
        layoutDict[module] = layout
      }
      if (main && !mainDict[module]) {
        mainDict[module] = true
        data.redirect[module] = id
      }
    }
  }
}

/**
 * @desc 生成树型结构菜单
 * @param {Array} menuArr 菜单数组
 * @returns {Array} 树型结构菜单
 */
function getMenuTree (menuArr) {
  let arrV1 = []
  // 排序
  // menuArr.sort(function (a, b) {
  //   return a.sort - b.sort
  // })
  // 生成顶级菜单
  for (let i = 0, l = menuArr.length; i < l; i++) {
    let item = menuArr[i]
    // 获得 routeName 字段
    item.routeName = item.id
    if (item.url === 'Login') {
      item.routeName = 'login'
    }
    // 获得 url 字段
    item.url = item.path
    // 获得 layout 字段
    item.layout = item.isLayer === '0'
    // 获得 main 字段
    item.main = item.isMain === '0'
    // 获得 hidden 字段
    item.hidden = item.visible !== '0'
    // 生成完整 url
    item.url = item.url ? '/' + item.url : ''
    item.ids = [item.id]
    if (item.top === true) {
      arrV1.push(item)
    }
  }
  // 判断是否为单个顶级菜单
  // let isSingle = (arrV1.length === 1)
  // 为顶级菜单添加子菜单
  for (let i = 0; i < arrV1.length; i++) {
    addChild(arrV1[i], menuArr)
  }

  // 为树结构添加子项
  function addChild (obj, arr) {
    obj.data = []
    for (let i = 0, l = arr.length; i < l; i++) {
      let item = arr[i]
      if (item.pid === obj.id) {
        // 生成url
        item.url = obj.url + item.url
        item.ids = obj.ids.concat(item.ids)
        // if (item.absolutePath) {
        //   item.url = item.absolutePath
        // }
        // 获取父元素icon
        // if (!isSingle) {
        //   item.icon = obj.icon
        // }
        // 添加子数据
        obj.data.push(item)
        addChild(item, arr)
      }
    }
  }

  return arrV1
}

export default router



