import ajax from '@/utils/http'
const ahead = '/api/upms/'
// 登陆
/**
 * @desc 获取验证码
 */
export const getVerification = (data) => ajax({
  url: ahead + 'sso/queryVerificationCode',
  method: 'post',
  data,
  responseType: 'json'
})

/**
 * @desc 登录
 * @param data
 * @returns {AxiosPromise}
 */
export const loginIn = (data) => ajax({
  url: ahead + 'sso/login',
  method: 'post',
  data,
  responseType: 'json'
})

/**
 * @desc 获取用户信息
 * @param data
 * @returns {AxiosPromise}
 */
export const queryUserInfo = () => ajax({
  url: ahead + 'sso/queryUserInfo',
  method: 'post',
  responseType: 'json',
  topMenu: true
})

/**
 * @desc 登出
 * @returns {AxiosPromise}
 */
export const logout = () => ajax({
  url: ahead + 'sso/logout',
  method: 'post',
  responseType: 'json',
  topMenu: true
})

// 区域编码
export const queryDivisions = (data) => ajax({
  url: ahead + 'divisions/queryDivisions',
  method: 'post',
  data,
  responseType: 'json',
  stringify: true
})

// 用户
/** 添加或修改用户 */
export const addOrModifyUser = (url, data) => ajax({
  url: ahead + url,
  method: 'post',
  data,
  responseType: 'json'
})
/** 逻辑删除用户 */
export const delUserList = (data) => ajax({
  url: ahead + 'user/removeUserByIdList',
  method: 'post',
  data,
  responseType: 'json'
})
/** 查询单个用户 */
export const queryUser = (data) => ajax({
  url: ahead + 'user/queryUser',
  method: 'post',
  data,
  responseType: 'json',
  stringify: true
})
/** 查询用户列表 */
export const queryUserList = (data) => ajax({
  url: ahead + 'user/queryUserList',
  method: 'post',
  data,
  responseType: 'json'
})
/** 用户密码修改 */
export const modifyPwd = (data) => ajax({
  url: ahead + 'user/modifyPwd',
  method: 'post',
  data,
  responseType: 'json',
  topMenu: true
})
/** 初始化用户密码 */
export const initPwd = (data) => ajax({
  url: ahead + 'user/initPwd',
  method: 'post',
  data,
  responseType: 'json'
})
/** 查询登录用户 */
export const queryLoginUser = (data) => ajax({
  url: ahead + 'user/queryLoginUser',
  method: 'post',
  data,
  responseType: 'json'
})
/** 修改登录用户信息 */
export const modifyLoginUser = (data) => ajax({
  url: ahead + 'user/modifyLoginUser',
  method: 'post',
  data,
  responseType: 'json'
})
// 角色
/**
 * 根据条件查询角色
 * @param data
 */
export const queryRole = (data) => ajax({
  url: ahead + 'role/queryBypage',
  method: 'post',
  data,
  responseType: 'json'
})
