import Vue from 'vue'

// 根目录，按模块打包时，1作用
export const rootDirectory = process.env.ROOT_ENV || ''

// 保存至 vue 实例中
Object.defineProperty(Vue.prototype, '$rootDirectory', {value: rootDirectory})

// 模块名
export const moduleName = process.env.MODULE_ENV



