import axios from 'axios'
import qs from 'qs'
import {Message} from 'element-ui'
import {getLoginInfo, clearLoginInfo, getFormatDate, objC, urlPushByRouteName, getMenuId, getMenuTopId} from './common'

const Axios = axios.create({
  // baseURL: '/api/', // 因为我本地做了反向代理
  baseURL: '/',
  timeout: 1000000,
  // headers: {'Content-Type': 'application/json;charset=utf-8'},
  responseType: 'json',
  withCredentials: true // 是否允许带cookie这些
})

// POST传参序列化(添加请求拦截器)
Axios.interceptors.request.use(
  config => {
    // 格式化时间
    if (config.data != null) {
      for (let keys in config.data) {
        if (config.data[keys] instanceof Date) {
          const date = config.data[keys]
          config.data[keys] = getFormatDate(date, 'YYYY/MM/DD hh:mm:ss')
        }
      }
    }
    // 在发送请求之前做某件事
    if (config.method === 'post') {
      // 参数处理
      if (config.data != null && config.data !== undefined) {
        for (let o in config.data) {
          if (config.data[o] == null || config.data[o] === undefined || config.data[o] === 'null' || config.data[o] === '') {
            delete config.data[o]
          }
        }
      }
      // 序列化
      if (config.stringify) {
        config.data = qs.stringify(config.data)
      }
      delete config.stringify
    }
    // config.headers.
    // 若是有做鉴权token , 就给头部带上token
    // 若是需要跨站点,存放到 cookie 会好一点,限制也没那么多,有些浏览环境限制了 localstorage 的使用
    // 这里localStorage一般是请求成功后我们自行写入到本地的,因为你放在vuex刷新就没了
    // 一些必要的数据写入本地,优先从本地读取
    let loginInfo = getLoginInfo()
    if (loginInfo.token) {
      config.headers.Authorization = loginInfo.token
    }
    if (loginInfo.UUID) {
      config.headers.code = loginInfo.UUID
    }
    // 菜单路由
    config.headers.menuId = getMenuId()
    if (config.topMenu) {
      config.headers.menuId = getMenuTopId(config.headers.menuId)
    }
    delete config.topMenu
    // 开发模式添加默认 ip
    if (process.env.NODE_ENV === 'development') {
      config.headers.ip = '192.168.10.36'
    }

    return config
  },
  error => {
    // error 的回调信息
    Message({
      //  消息弹窗组件,类似toast
      showClose: true,
      message: error && error.data.error.message,
      type: 'error'
    })
    return Promise.reject(error.data.error.message)
  }
)

// 返回状态判断(添加响应拦截器)
Axios.interceptors.response.use(
  res => {
    // 对响应数据做些事
    // 1001001006，token异常
    // 1001001007,Token 为空
    // 1001001008,Token 过期
    // 1001001009,没有权限
    let failedCode = {
      1001001006: true,
      1001001007: true,
      1001001008: true,
      1001001009: true
    }
    if (failedCode[objC(res, 'data', 'code')]) {
      Message({
        //  消息弹窗组件,类似toast
        showClose: true,
        message: objC(res, 'data', 'message') || '用户登录过期，请重新登录！',
        type: 'error'
      })
      // 清除登录状态
      clearLoginInfo()
      // 跳转到登录页面
      // this.$router.push('/login')
      // window.location.href = './login'
      urlPushByRouteName('login')
      // router.push('/components/login')
      // router.replace({
      //   name: 'login',
      //   query: {redirect: router.currentRoute.fullPath}
      // })
      return Promise.reject(res.data)
    }
    return res
  },
  error => {
    // 返回 response 里的错误信息
    // let errorInfo = error.data.error ? error.data.error.message : error.data
    let errorInfo = objC(error.data, 'error', 'message') || error.data
    return Promise.reject(errorInfo)
  }
)

export default Axios



