<template>
  <div class="sidebar-logo-container" :class="{'collapse':collapse}">
    <transition name="sidebarLogoFade">
      <router-link v-if="collapse" key="collapse" class="sidebar-logo-link" to="/">
        <img v-if="logo" :src="logo" class="sidebar-logo">
        <h1 v-else class="sidebar-title">{{ title }} </h1>
      </router-link>
      <router-link v-else key="expand" class="sidebar-logo-link" to="/">
        <img v-if="logo" :src="logo" class="sidebar-logo">
        <h1 class="sidebar-title">{{ title }} </h1>
      </router-link>
    </transition>
  </div>
</template>

<script>
import Logo from '@/assets/images/logo.png'
export default {
  name: 'SidebarLogo',
  props: {
    collapse: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      title: '中科弘清',
      logo: Logo
    }
  }
}
</script>

<style lang="css" scoped>
  .sidebarLogoFade-enter-active {
    transition: opacity 1.5s;
  }

  .sidebarLogoFade-enter,
  .sidebarLogoFade-leave-to {
    opacity: 0;
  }

  .sidebar-logo-container {
    position: relative;
    width: 100%;
    /*height: 50px;*/
    /*line-height: 50px;*/
    height: 64px;
    line-height: 64px;
    /*text-align: center;*/
    text-align: left;
    padding-left: 24px;
    overflow: hidden;
  }

  .hideSidebar .sidebar-logo-container {
    text-align: center;
    padding-left: 0px;
  }

  .sidebar-logo-container .sidebar-logo-link {
    height: 100%;
    width: 100%;
  }

  .sidebar-logo-container .sidebar-logo-link .sidebar-logo {
    width: 32px;
    height: 32px;
    vertical-align: middle;
    margin-right: 6px;
  }

  .sidebar-logo-container .sidebar-logo-link .sidebar-title {
    display: inline-block;
    margin: 0;
    color: #fff;
    font-weight: 600;
    line-height: 50px;
    font-size: 14px;
    font-family: Avenir, Helvetica Neue, Arial, Helvetica, sans-serif;
    vertical-align: middle;
  }

  .sidebar-logo-container.collapse .sidebar-logo {
    margin-right: 0px;
  }

</style>



