<template>
  <div class="navbar">
    <hamburger id="hamburger-container" :is-active="sidebar.opened" class="hamburger-container" @toggleClick="toggleSideBar" />

    <breadcrumb id="breadcrumb-container" class="breadcrumb-container" />

    <div class="right-menu">
      <template v-if="device!=='mobile'">

        <search id="header-search" class="right-menu-item" />

        <HeaderNotice class="right-menu-item"></HeaderNotice>

<!--        <el-tooltip content="源码地址" effect="dark" placement="bottom">-->
<!--          <Github class="right-menu-item hover-effect" />-->
<!--        </el-tooltip>-->

<!--        <el-tooltip content="全屏缩放" effect="dark" placement="bottom">-->
<!--          <screenfull id="screenfull" class="right-menu-item hover-effect" />-->
<!--        </el-tooltip>-->

<!--        <el-tooltip content="布局设置" effect="dark" placement="bottom">-->
<!--          <size-select id="size-select" class="right-menu-item hover-effect" />-->
<!--        </el-tooltip>-->

      </template>

      <el-dropdown class="avatar-container right-menu-item hover-effect" trigger="click">
        <div class="avatar-wrapper">
<!--          <img :src="user.avatar ? baseApi + '/avatar/' + user.avatar : Avatar" class="user-avatar">-->
          <img :src="Avatar" class="user-avatar">
          <span class="name-avatar">{{name}}</span>
<!--          <i class="el-icon-caret-bottom" />-->
        </div>
        <el-dropdown-menu
          slot="dropdown"
          :visible-arrow="false"
          class="header-avatar-popper"
        >
<!--          <a target="_blank" href="https://docs.auauz.net/">-->
<!--            <el-dropdown-item>-->
<!--              项目文档-->
<!--            </el-dropdown-item>-->
<!--          </a>-->
<!--          <span style="display:block;" @click="show = true">-->
<!--            <el-dropdown-item>-->
<!--              布局设置-->
<!--            </el-dropdown-item>-->
<!--          </span>-->
<!--          <router-link to="/user/center">-->
<!--            <el-dropdown-item>-->
<!--              个人中心-->
<!--            </el-dropdown-item>-->
<!--          </router-link>-->
          <el-dropdown-item v-buttonshow="'upms:index:info'">
            <ant-icon icon-class="UserOutlined"></ant-icon>
            个人中心
          </el-dropdown-item>
          <el-dropdown-item @click.native="changePassword" v-buttonshow="'upms:index:change'">
            <ant-icon icon-class="EditOutlined"></ant-icon>
            修改密码
          </el-dropdown-item>
          <span style="display:block;" @click="open" v-buttonshow="'upms:index:logout'">
            <el-dropdown-item divided>
            <ant-icon icon-class="LoginOutlined"></ant-icon>
              退出登录
            </el-dropdown-item>
          </span>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

    <PasswordChange ref="passwordDialog"></PasswordChange>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Hamburger from '@/components/Hamburger'
import Breadcrumb from '@/components/Breadcrumb'
import Search from '@/components/HeaderSearch'
import HeaderNotice from '@/components/HeaderNotice'
import PasswordChange from '@/components/PasswordChange/index'
import Avatar from '@/assets/images/avatar.png'

export default {
  components: {
    Hamburger,
    Breadcrumb,
    Search,
    HeaderNotice,
    PasswordChange
  },
  data () {
    return {
      Avatar: Avatar,
      name: 'admin',
      dialogVisible: false
    }
  },
  computed: {
    ...mapState({
      sidebar: state => state.app.sidebar,
      device: state => state.app.device
    })
  },
  // beforeRouteEnter (to, from, next) {
  //   next(vm => {
  //     vm.queryUserInfo()
  //   })
  // },
  beforeMount () {
    this.queryUserInfo()
  },
  methods: {
    queryUserInfo () {
      this.$api.queryUserInfo().then((response) => {
        this.name = response.data.data
      })
    },
    toggleSideBar () {
      this.$store.dispatch('app/toggleSideBar')
    },
    open () {
      this.$confirm('确定注销并退出系统吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.logout()
      })
    },
    logout () {
      this.$api.logout().then((response) => {
        if (response.data.code === 0) {
          // 清除登录状态
          this.$common.clearLoginInfo()
          // 跳至登录页
          this.$common.urlPushByRouteName('login')
        } else {
          this.$message.error('注销失败，请重试')
        }
      }).catch((response) => {
        console.log(response)
      })
    },
    changePassword () {
      this.$refs.passwordDialog.open()
    }
  }
}
</script>

<style lang="css" scoped>
  .navbar {
    /*height: 50px;*/
    height: 64px;
    overflow: hidden;
    position: relative;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  }

  .navbar .hamburger-container {
    /*line-height: 46px;*/
    line-height: 60px;
    height: 100%;
    float: left;
    cursor: pointer;
    transition: background .3s;
    -webkit-tap-highlight-color: transparent;
  }

  .navbar .hamburger-container:hover {
    background: rgba(0, 0, 0, 0.025);
  }

  .navbar .breadcrumb-container {
    float: left;
  }

  .navbar .errLog-container {
    display: inline-block;
    vertical-align: top;
  }

  .navbar .right-menu {
    float: right;
    height: 100%;
    /*line-height: 50px;*/
    line-height: 64px;
  }

  .navbar .right-menu:focus {
    outline: none;
  }

  .navbar .right-menu .right-menu-item {
    display: inline-block;
    /*padding: 0 8px;*/
    height: 100%;
    font-size: 18px;
    color: #5a5e66;
    vertical-align: text-bottom;
    padding: 0 13px;
    float: left;
  }

  .navbar .right-menu .right-menu-item.hover-effect {
    cursor: pointer;
    transition: background .3s;
  }

  .navbar .right-menu .right-menu-item.hover-effect:hover {
    background: rgba(0, 0, 0, 0.025);
  }

  .navbar .right-menu .avatar-container {
    margin-right: 30px;
  }

  .navbar .right-menu .avatar-container .avatar-wrapper {
    /*margin-top: 5px;*/
    /*position: relative;*/
    height: 100%;
  }

  .navbar .right-menu .avatar-container .avatar-wrapper .user-avatar {
    /*cursor: pointer;*/
    /*width: 40px;*/
    /*height: 40px;*/
    /*border-radius: 10px;*/
    width: 26px;
    height: 26px;
    border-radius: 13px;
    margin: 19px 10px 0 0;
  }

  .navbar .right-menu .avatar-container .avatar-wrapper .name-avatar {
    float: right;
    font-size: 14px;
    color: #373737;
  }

  .navbar .right-menu .avatar-container .avatar-wrapper .el-icon-caret-bottom {
    cursor: pointer;
    position: absolute;
    right: -20px;
    top: 25px;
    font-size: 12px;
  }

</style>
<style>
  .el-popper.header-avatar-popper {
    margin-top: 4px;
    /*border: 0;*/
  }
</style>



