import 'babel-polyfill'
import App from '@/App'
import Vue from 'vue'
import router from '@/router'
import store from '@/store'
import '@/utils/httpPlugin'
import {beforeunload, getFormatDate} from '@/utils/common'
import '@/utils/commonPlugin'
import '@/utils/rootDirectory'
import '@/utils/buttonshow'
import * as api from '@/api'

import Cookies from 'js-cookie'

import 'normalize.css/normalize.css'
import ElementUI from 'element-ui'
import '@/assets/styles/element-variables.css'
// global css
import '@/assets/styles/index.css'

// 代码高亮
import VueHighlightJS from 'vue-highlightjs'
import 'highlight.js/styles/atom-one-dark.css'

import cryptos from 'crypto-js'
import '@/utils/highcharts'
import echarts from 'echarts'
import leaflet from '@/components/leaflet'
import 'leaflet/dist/leaflet.css'

import './assets/icons' // icon

import AntIcon from '@/components/SvgIcon/AntIcon'// svg component

// register globally
Vue.component('AntIcon', AntIcon)

Vue.prototype.$cryptos = cryptos
Vue.config.productionTip = false

Vue.use(ElementUI, {
  size: Cookies.get('size') || 'small' // set element-ui default size
})
Vue.use(VueHighlightJS)
Object.defineProperty(Vue.prototype, '$echarts', {value: echarts})
Vue.use(leaflet.plugin, store)
Object.defineProperty(Vue.prototype, '$api', {value: api})

// mock
// if (process.env.NODE_ENV === 'development') {
//   require('../mock/index.js')
// }

// 当关闭页面时，删除 token
beforeunload(function () {
  localStorage[getFormatDate(new Date(), 'YMDhms')] = 'delete'
})

/* eslint-disable no-new */

new Vue({
  el: '#app',
  store,
  router,
  render: h => h(App)
})



