/**
 * UTC时间转为格式化日期 主要针对influx取出数据转换
 * @param date UTC时间
 * @param fmt 格式
 * @returns {*}
 */
export function utcTimeFormat (date, fmt) {
  return timeFormat(dateAdd(new Date(date), 'h', -8), fmt)
}

/**
 * 时间戳转为格式化日期
 * @param time 时间戳
 * @param fmt 格式
 * @returns {*}
 */
export function timeFormat (time, fmt) {
  let date = new Date(time)
  if (fmt == null) {
    if (date.getHours() === 0) {
      fmt = 'MM/dd'
    } else {
      fmt = 'hh:mm'
    }
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    let str = o[k] + ''
    if (new RegExp(`(${k})`).test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
    }
  }
  return fmt
}

export function padLeftZero (str) {
  return ('00' + str).substr(str.length)
}

/**
 * jsonArray根据字段排序
 * @param array 数据
 * @param field 需要排序字段key
 * @param reverse 是否倒序 false正  true 倒序
 * @returns {*}
 */
export function jsonSort (array, field, reverse) {
  // 数组长度小于2 或 没有指定排序字段 或 不是json格式数据
  if (array.length < 2 || !field || !isObject(array[0])) return array
  // 数字类型排序
  if (typeof array[0][field] === 'number') {
    array.sort(function (x, y) {
      return x[field] - y[field]
    })
  }
  // 字符串类型排序
  if (typeof array[0][field] === 'string') {
    array.sort(function (x, y) {
      return x[field].localeCompare(y[field])
    })
  }
  // 倒序
  if (reverse) {
    array.reverse()
  }
  return array
}

/**
 * jsonArray对象多字段排序 let orderArr = sortObjectArray(dataObj,['path_cate','path_ver','path_id'],"asc")
 * @param objArr array数据
 * @param keyArr 排序keys
 * @param type 排序 asc 或 desc
 * @returns {*}
 */
export function sortObjectArray (objArr, keyArr, type) {
  for (let x = 0, l = objArr.length; x < l; x++) {
    objArr[x].oldIndex = x
  }
  if (type !== undefined && type !== 'asc' && type !== 'desc') {
    return 'error'
  }
  let order = 1
  if (type !== undefined && type === 'desc') {
    order = -1
  }
  let key = keyArr[0]
  objArr.sort(function (objA, objB) {
    if (objA[key] > objB[key]) {
      return order
    } else if (objA[key] < objB[key]) {
      return 0 - order
    } else {
      if (objA.oldIndex > objB.oldIndex) {
        return order
      } else if (objA.oldIndex < objB.oldIndex) {
        return 0 - order
      } else {
        return 0
      }
    }
  })

  for (let i = 1, l = keyArr.length; i < l; i++) {
    let nkey = keyArr[i]
    for (let y = 0, l2 = objArr.length; y < l2; y++) {
      objArr[y].oldIndex = y
    }
    objArr.sort(function (objA, objB) {
      for (let j = 0; j < i; j++) {
        if (objA[keyArr[j]] !== objB[keyArr[j]]) {
          if (objA.oldIndex > objB.oldIndex) {
            return order
          } else if (objA.oldIndex < objB.oldIndex) {
            return 0 - order
          } else {
            return 0
          }
        }
      }
      if (objA[nkey] > objB[nkey]) {
        return order
      } else if (objA[nkey] < objB[nkey]) {
        return 0 - order
      } else {
        if (objA.oldIndex > objB.oldIndex) {
          return order
        } else if (objA.oldIndex < objB.oldIndex) {
          return 0 - order
        } else {
          return 0
        }
      }
    })
  }
  return objArr
}

