/**
 * 格式化污染物的显示
 * @param pol 污染物 no2,so2,co,o3,o3_8h,pm25,pm10
 * @returns {string}
 */
export function getPolLabel (pol) {
  let ret = ''
  pol = pol.toLocaleLowerCase()
  switch (pol) {
    case 'no2':
      ret = 'NO<sub>2</sub>'
      break
    case 'so2':
      ret = 'SO<sub>2</sub>'
      break
    case 'co':
      ret = 'CO'
      break
    case 'o3':
      ret = 'O<sub>3</sub>'
      break
    case 'o3_8h':
      ret = 'O<sub>3</sub>_8h'
      break
    case 'pm25':
      ret = 'PM<sub>2.5</sub>'
      break
    case 'pm10':
      ret = 'PM<sub>10</sub>'
      break
    case 'aqi':
      ret = 'AQI'
      break
    default:
      ret = ''
      break
  }
  return ret
}

/**
 * 字符串转时间
 * @param str  yyyy-MM-dd HH:mm:ss
 * @returns {Date}
 */
export function stringToDate (str) {
  let year = str.substr(0, 4)
  let month = str.substr(5, 2)
  let date = str.substr(8, 2)
  let hour = str.substr(11, 2)
  let min = str.substr(14, 2)

  let reDate = new Date()
  reDate.setYear(year)
  reDate.setMonth(month - 1, 1)// 实际月份减一
  reDate.setDate(date)
  reDate.setHours(hour)
  reDate.setMinutes(min)
  reDate.setSeconds('00')
  reDate.setMilliseconds(0)
  return reDate
}

/**
 *
 * @param time 时间
 * @param interval 计算跨度
 * @param number 计算量
 * @returns {*} 时间
 */
export function dateAdd (time, interval, number) {
  switch (interval) {
    case 'y':
      time = setYear(time, number)
      break
    case 'm':
      time = setMonth(time, number)
      break
    case 'w':
      time = setWeek(time, number)
      break
    case 'd':
      time = setDay(time, number)
      break
    case 'h':
      time = setHour(time, number)
      break
    case 'n':
      time = setMinutes(time, number)
      break
    default:
      time = new Date()
      break
  }
  return time
}

/**
 * 计划时间月
 * @param time 时间
 * @param number 月量
 * @returns {number}
 */
export function setMonth (time, number) {
  return time.setMonth(time.getMonth + number)
}

/**
 * 计划时间周
 * @param time 时间
 * @param number 周量
 * @returns {number}
 */
export function setWeek (time, number) {
  return time.setDate(time.getDate() * 7 + number)
}

/**
 * 计划时间天
 * @param time 时间
 * @param number 天量
 * @returns {number}
 */
export function setDay (time, number) {
  return time.setDate(time.getDate() + number)
}

/**
 * 计划时间小时
 * @param time 时间
 * @param number 小时量
 * @returns {number}
 */
export function setHour (time, number) {
  return time.setHours(time.getHours() + number)
}

/**
 * 计划时间分钟
 * @param time 时间
 * @param number 年量
 * @returns {number}
 */
export function setMinutes (time, number) {
  return time.setMinutes(time.setMinutes() + number)
}

/**
 * 计划时间年
 * @param time 时间
 * @param number 年量
 * @returns {number}
 */
export function setYear (time, number) {
  return time.setFullYear(time.getFullYear() + number)
}

/**
 * 数据分组
 * @param array
 * @param f
 * @returns {any[]}
 * 调用方式
 * const sorted = this.$common.groupBy(this.data, function (item) {
          return [item.citycode]
        })
 console.log(sorted)
 */
export function groupBy (array, f) {
  const groups = {}
  array.forEach(function (o) {
    const group = JSON.stringify(f(o))
    groups[group] = groups[group] || []
    groups[group].push(o)
  })
  return Object.keys(groups).map(function (group) {
    return groups[group]
  })
}

