/**
 * 获取指定日期月的第一天
 * @param date
 * @returns {Date}
 */
export function getCurrentMonthFirst (date) {
  date = new Date(date.getTime())
  date.setMonth(date.getMonth())
  date.setDate(1)
  return date
}

/**
 * 获取指定日期月的最后一天
 * @param date
 * @returns {Date}
 */
export function getCurrentMonthLast (date) {
  date = new Date(date.getTime())
  let currentMonth = date.getMonth()
  let nextMonth = ++currentMonth
  let nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1)
  let oneDay = 1000 * 60 * 60 * 24
  return new Date(nextMonthFirstDay - oneDay)
}

/**
 * 根据污染等级获取颜色
 * @param val 等级数值或汉字
 */
export function getLevelColor (val) {
  // 默认无效灰色
  let color = '#D1D1D1'
  if (val === '1' || val === '优') {
    color = '#00e400'
  } else if (val === '2' || val === '良') {
    color = '#ffff00'
  } else if (val === '3' || val.indexOf('轻度') >= 0) {
    color = '#ff7e00'
  } else if (val === '4' || val.indexOf('中度') >= 0) {
    color = '#ff0000'
  } else if (val === '5' || val.indexOf('重度') >= 0) {
    color = '#99004c'
  } else if (val === '6' || val.indexOf('严重') >= 0) {
    color = '#7e0023'
  }
  return color
}

