/**
 * 根据XY数据获取一元回归方程，相关系数，回归线
 * @param arrayXY
 */
export function getLinearRegression (arrayXY) {
  if (arrayXY === null || arrayXY === undefined || !(arrayXY instanceof Array) || arrayXY.length === 0) {
    return {
      result: false
    }
  }
  // 计算X和Y的均值
  let sumX = 0.0
  let sumY = 0.0
  let count = 0
  let sumXY = 0.0
  let sumXX = 0.0
  let minX = 0.0
  let maxX = 0.0
  for (let i = 0, l = arrayXY.length; i < l; i = i + 1) {
    if (arrayXY[i][0] >= 0 && arrayXY[i][1] >= 0) {
      sumX = sumX + arrayXY[i][0]
      sumY = sumY + arrayXY[i][1]
      count = count + 1
      sumXY = sumXY + arrayXY[i][0] * arrayXY[i][1]
      sumXX = sumXX + Math.pow(arrayXY[i][0], 2)
      if (count === 1) {
        minX = arrayXY[i][0]
        maxX = arrayXY[i][0]
      } else {
        if (arrayXY[i][0] < minX) {
          minX = arrayXY[i][0]
        }
        if (arrayXY[i][0] > maxX) {
          maxX = arrayXY[i][0]
        }
      }
    }
  }
  if (count === 0) {
    return {
      result: false
    }
  }
  let avgX = sumX / count
  let avgY = sumY / count
  // 计算变量b
  let b = (sumXY - count * avgX * avgY) / (sumXX - count * Math.pow(avgX, 2))
  let a = avgY - b * avgX
  let rPart1 = 0.0
  let rPart2 = 0.0
  let rPart3 = 0.0
  for (let i = 0, l = arrayXY.length; i < l; i = i + 1) {
    if (arrayXY[i][0] >= 0 && arrayXY[i][1] >= 0) {
      rPart1 = rPart1 + (arrayXY[i][0] - avgX) * (arrayXY[i][1] - avgY)
      rPart2 = rPart2 + Math.pow(arrayXY[i][0] - avgX, 2)
      rPart3 = rPart3 + Math.pow(arrayXY[i][1] - avgY, 2)
    }
  }
  let r2 = Math.pow((rPart1), 2) / (rPart2 * rPart3)
  let minY = Math.round((a + b * minX) * Math.pow(10, 3)) / Math.pow(10, 3)
  let maxY = Math.round((a + b * maxX) * Math.pow(10, 3)) / Math.pow(10, 3)
  b = Math.round(b * Math.pow(10, 3)) / Math.pow(10, 3)
  a = Math.round(a * Math.pow(10, 3)) / Math.pow(10, 3)
  r2 = Math.round(r2 * Math.pow(10, 3)) / Math.pow(10, 3)
  minX = Math.round(minX * Math.pow(10, 3)) / Math.pow(10, 3)
  maxX = Math.round(maxX * Math.pow(10, 3)) / Math.pow(10, 3)
  minY = Math.round(minY * Math.pow(10, 3)) / Math.pow(10, 3)
  maxY = Math.round(maxY * Math.pow(10, 3)) / Math.pow(10, 3)
  let fuhao = a < 0 ? '-' : '+'
  a = Math.abs(a)
  return {
    result: true,
    equation: 'y=' + b + 'x' + fuhao + a,
    R2: '（R²=' + r2 + '）',
    min: [minX, minY],
    max: [maxX, maxY]
  }
}

/**
 * @desc 计算特定百分位
 * @param arr
 * @param number
 * @returns {number}
 */
export function getSpecialData (arr, number) {
  // let specialdata = 0
  // if (arr.length === 1) {
  //   specialdata = arr[0]
  // } else {
  //   let mark = (arr.length) * number
  //   let front = Math.floor(mark) - 1
  //   let back = Math.ceil(mark) - 1
  //   if (front === back) {
  //     specialdata = arr[front]
  //   } else {
  //     specialdata = Number((arr[front] * (back - mark + 1) + arr[back] * (mark - front - 1)).toFixed(3))
  //   }
  // }
  // return specialdata
  let specialdata = 0
  if (arr != null && arr.length > 0) {
    let count = arr.length
    if (count === 1) {
      specialdata = arr[0]
    } else {
      let mark = 1 + (count - 1) * number
      let min = Math.floor(mark)
      let max = Math.ceil(mark)
      if (min === max) {
        specialdata = arr[min - 1]
      } else {
        specialdata = Number((arr[min - 1] + (arr[max - 1] - arr[min - 1]) * (mark - min)).toFixed(3))
      }
    }
  }
  return Number(specialdata.toFixed(3))
}

