/**
 * @desc removeClass
 * @param {object} el HTML标签对象
 * @param {String} className 类名
 */
export function removeClass (el, className) {
  let str = notUndefined(objC(el, 'className'))

  if (!str) {
    return
  }

  let arr = str.split(' ').filter(function (val) {
    return val && val !== className
  })

  el.className = arr.join(' ')
}

