/**
 * @desc 多级对象设置 objSet(value,obj,'b','c')
 * @param {*} val 赋予的值
 * @param {Object} obj 被赋予的对象
 * @param {Array} arr 多级的键值
 * @returns {Object} 被赋予后的对象
 */
export function objSet (val, obj, ...arr) {
  if (arr.length <= 0) {
    return {}
  }
  if (!isObject(obj)) {
    obj = {}
  }
  let tempObj = obj
  let k
  for (let i = 0, l = arr.length - 1; i < l; i++) {
    k = arr[i]
    if (!isObject(tempObj[k])) {
      tempObj[k] = {}
    }
    tempObj = tempObj[k]
  }
  k = arr[arr.length - 1]
  tempObj[k] = val
  return obj
}

/**
 * @desc 对象取值安全方法
 * @param {Object} obj 对象
 * @param {Array} arr 多级的键值
 * @returns {*} 取出的值或 ''
 */
export function objC (obj, ...arr) {
  if (!arr.length) {
    return ''
  }
  for (let i = 0, l = arr.length; i < l; i++) {
    if (!isObject(obj)) {
      return ''
    }
    obj = obj[arr[i]]
  }
  return notUndefined(obj)
}

/**
 * @desc 保证函数执行
 * @param fn 函数
 * @returns {Function|*}
 */
export function fnC (fn) {
  if (typeof fn === 'function') {
    return fn
  } else {
    return () => {
      console.log('not a function')
    }
  }
}

/**
 * @desc 将 undefined 和 nell 改为 ''
 * @param {*} str 需要保障的值
 * @param {*} u 替换的值
 * @returns {string|*} 保障后的值
 */
export function notUndefined (str, u) {
  if (u === undefined || u === null) {
    u = ''
  }
  if (!str && str !== 0) {
    return u
  }
  return str
}

/**
 * @desc 对象的复制
 * @param obj 对象
 * @returns {Object} 复制的对象
 */
export function deepCopy (obj) {
  if (!isObject(obj)) return
  let newObj = obj instanceof Array ? [] : {}
  for (let key in obj) {
    // 判断是否含有当前属性
    if (obj.hasOwnProperty(key)) {
      // 判断，是对象的话再次 deepCopy ，不然直接复制
      newObj[key] = isObject(obj[key]) ? deepCopy(obj[key]) : obj[key]
    }
  }
  return newObj
}

/**
 * @desc 彻底冻结对象
 * @param {Object} obj 对象
 */
export function constantize (obj) {
  if (!isObject(obj)) {
    return
  }
  Object.freeze(obj)
  Object.keys(obj).forEach(function (key, i) {
    constantize(obj[key])
  })
}

/**
 * @desc 获取顶层对象（浏览器是 window，nodejs 是 global，self 在两个中的较新版本中都能用）
 * @returns {(WorkerGlobalScope | Window)|Window} 顶层对象
 */
export function getGlobal () {
  if (typeof self !== 'undefined') {
    return self
  }
  if (typeof window !== 'undefined') {
    return window
  }
  if (typeof global !== 'undefined') {
    return global
  }
  throw new Error('unable to locate global object')
}

/**
 * @desc 一个字符是否是 4 个字节 0x 表示 16 位 0o 表示 8 位 0b 表示两位
 * @param {Number|String} c 单字符
 * @returns {boolean} 是否是 4 个字节
 */
export function is32Bit (c) {
  return c.codePointAt(0) > 0xffff
}

/**
 * @desc 根据日期对象返回年月日时分秒
 * @param {Date} d 日期对象
 * @param {String} fmt 日期格式（YMDhmsSq 年月日时分秒毫秒季度）
 * @returns {String} 遵照日期格式的日期字符串
 */
export function getFormatDate (d, fmt) {
  // author: meizz
  let o = {
    // 年份
    'Y+': d.getFullYear(),
    // 月份
    'M+': d.getMonth() + 1,
    // 日
    'D+': d.getDate(),
    // 小时
    'h+': d.getHours(),
    // 分
    'm+': d.getMinutes(),
    // 秒
    's+': d.getSeconds(),
    // 季度
    'q+': Math.floor((d.getMonth() + 3) / 3),
    // 毫秒
    'S': d.getMilliseconds()
  }

  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length <= (o[k] + '').length) ? (o[k]) : (('000' + o[k]).substr(-RegExp.$1.length)))
      // fmt = fmt.replace(RegExp.$1, o[k].padStart(RegExp.$1.length, 0))
    }
  }
  return fmt
}

/**
 * @desc 标签模板，返回原值
 * @param literals
 * @returns {string|string}
 */
export function passthru (literals) {
  let result = ''
  let i = 0

  while (i < literals.length) {
    result += literals[i++]
    if (i < arguments.length) {
      result += arguments[i]
    }
  }

  return result
}

/**
 * @desc 判断是否为数组
 * @param {*} arr 被判断的值
 * @returns {boolean} 是否为数组
 */
export function isArray (arr) {
  // 1、array属于引用型数据，在传递过程中，仅仅是引用地址的传递。
  // 2、每个页面的Array原生对象所引用的地址是不一样的，在子页面声明的array，所对应的构造函数，是子页面的Array对象；
  //    父页面来进行判断，使用的Array并不等于子页面的Array；切记，不然很难跟踪问题！
  // return arr instanceof Array

  // 同上
  // return arr.constructor == Array

  // 同上，并且 __proto__ 不兼容低版本 ie 浏览器
  // return arr.__proto__.constructor == Array

  return Object.prototype.toString.call(arr) === '[object Array]'
}

/**
 * @desc 判断是否为对象
 * @param {*} obj 被判断的值
 * @returns {boolean} 是否为对象
 */
export function isObject (obj) {
  if (typeof obj === 'object' && obj !== null) {
    return true
  }
  return false
}

/**
 * @desc 小数转化为百分比字符串
 * @param {Number} num 浮点数
 * @param {Number} l 保留小数位（最多再加两位）
 * @param isNum
 * @returns {string|number|string}
 */
export function percentage (num, l, isNum) {
  num = parseFloat(num) || 0
  l = l || 2
  let str = ''
  num = num * 100
  str = fixedNumArea(num, l + 2, l)
  if (isNum) {
    return str
  }
  return str + '%'
}

/**
 * @desc 有范围的保留小数（有范围是为了尽量不输出 0 ）fixedNumArea(0.004,2,4)
 * @param {Number} num 浮点数
 * @param {Number} Max 保留最多位数
 * @param {Number} min 保留最少位数
 * @returns {string|number} 保留n位小数的浮点数
 */
export function fixedNumArea (num, max, min) {
  // 无数据处理
  if (!num) {
    return 0
  }
  min = min || 0
  min = fixedNum(min, 0)
  if (max !== 0 && max !== '0' && !max) {
    max = 2
  }
  max = fixedNum(max, 0)
  // 临时数据
  let temp
  for (let i = min; i <= max; i++) {
    temp = fixedNum(num, i)
    if (temp) {
      return temp
    }
  }
  return fixedNum(num, 0)
}

/**
 * @desc 保留n位小数 fixedNum(1.234,2)
 * @param {Number} num 浮点数
 * @param {Number} n 保留位数
 * @param {Boolean} isStr 是否输出字符串
 * @returns {string|number} 保留n位小数的浮点数
 */
export function fixedNum (num, n = 2, isStr) {
  n = parseInt(n)
  if (isNaN(n)) {
    n = 2
  }

  num = parseFloat(num) || 0
  num = num.toFixed(n)
  if (isStr) {
    return num
  }
  num = parseFloat(num) || 0
  return num
}

/**
 * @desc 获取几天后的日期对象
 * @param {Number} num 天数
 * @param {Date} todayDate 日期对象（默认今天）
 * @returns {Date} 几天后的日期对象
 */
export function laterObj (num, todayDate) {
  if (num && typeof num !== 'number') {
    num = parseInt(num)
  }
  num = num || 0
  let d = todayDate ? new Date(todayDate) : new Date()
  let date = d.getDate()
  d.setDate(date + num)
  return d
}

/**
 * @desc 获取几年后的日期对象
 * @param {Number} num 天数
 * @param {Date} todayDate 日期对象（默认今天）
 * @returns {Date} 几年后的日期对象
 */
export function laterObjByY (num, todayDate) {
  if (num && typeof num !== 'number') {
    num = parseInt(num)
  }
  num = num || 0
  let d = todayDate ? new Date(todayDate) : new Date()
  let date = d.getFullYear()
  d.setFullYear(date + num)
  return d
}

