/**
 * @desc 计算天数差
 * @param {Date} d1 日期对象1
 * @param {Date} d2 日期对象2
 * @returns {boolean} 天数差
 */
export function DateDiff (d1, d2) {
  // 把相差的毫秒数转换为天数
  let iDays = parseInt(Math.abs(d1.getTime() - d2.getTime()) / 1000 / 60 / 60 / 24)
  return iDays
}

/**
 * @desc 计算小时差
 * @param {Date} d1 日期对象1
 * @param {Date} d2 日期对象2
 * @returns {boolean} 小时差
 */
export function DateDiffByHour (d1, d2) {
  // 把相差的毫秒数转换为天数
  let ihours = parseInt(Math.abs(d1.getTime() - d2.getTime()) / 1000 / 60 / 60)
  return ihours
}

/**
 * @desc 鼠标距离元素左侧的距离
 * @param {Object} dom HTML元素对象
 * @param {Object} e 事件对象，用来获取鼠标距离最左边的距离
 * @returns {Number} 鼠标距离元素左侧的距离
 */
export function domMouseX (dom, e) {
  let arr = domParents(dom)
  arr.push(dom)
  let w = objC(e, 'pageX')
  for (let i = 0, l = arr.length; i < l; i++) {
    w -= arr[i].offsetLeft || 0
  }
  return w
}

/**
 * @desc 获取 dom 元素的所有父元素
 * @param {object} dom HTML元素对象
 * @returns {Array} dom 元素的所有父元素
 */
export function domParents (dom) {
  let arr = []
  while (dom.parentElement) {
    let p = dom.parentElement
    arr.push(p)
    dom = p
  }
  return arr
}

/**
 * @desc 判断是否为正确日期对象，而不是 Invalid Date
 * @param {Date} date 日期对象
 * @returns {boolean} 是否为正确日期对象
 */
export function isValidDate (date) {
  return date instanceof Date && !isNaN(date.getTime())
}

/**
 * @desc 判断两个日期对象是否相同（对比年月日时分秒）
 * @param {Date} d1 日期对象1
 * @param {Date} d2 日期对象2
 * @returns {boolean} 两个日期对象是否相同
 */
export function isSameDate (d1, d2) {
  if (isValidDate(d1) && isValidDate(d2)) {
    let f1 = getFormatDate(d1, 'YMDhms')
    let f2 = getFormatDate(d2, 'YMDhms')
    return f1 === f2
  } else {
    return false
  }
}

/**
 * @desc 模板编译函数
 * @param {String} template 模板
 * let template = `
 <ul>
 <% for(let i=0; i < data.length; i++) { %>
    <li><%= data[i] %></li>
  <% } %>
 </ul>
 `
 * @returns {string} 字符串形式的代码。通过 eval(compile (template)) 获得函数 parse
 */
export function compile (template) {
  const evalExpr = /<%=(.+?)%>/g
  const expr = /<%([\s\S]+?)%>/g

  template = template
    .replace(evalExpr, '`) \n  echo( $1 ) \n  echo(`')
    .replace(expr, '`) \n $1 \n  echo(`')

  template = 'echo(`' + template + '`)'

  let script =
    `(function parse(data){
    let output = ""

    function echo(html){
      output += html
    }

    ${template}

    return output
  })`

  return script
}

/**
 * @desc 判断是否是 api 接口函数
 * @param {function} api api接口函数
 * @returns {boolean} 是否是 api 接口函数
 */
export function isApi (api) {
  // console.log('allApi:', allApi)
  // console.log('api.name:', api.name)
  return allApi[api.name] === api
}

/**
 * @desc 跳转页面 ，通过路径
 * @param url 路径
 */
export function urlPush (url) {
  if (onModuleByUrl(url)) {
    router.push(url)
  } else {
    location.href = getPathnameByUrl(url)
  }
}

/**
 * @desc 判断路径是否在当前模块(开发模式不分模块)
 * @param url 路径
 * @returns {boolean}
 */
export function onModuleByUrl (url) {
  let module = store.getters['menu/menuUrlToModule'](url)
  return process.env.NODE_ENV === 'development' || !moduleName || module === moduleName
}

/**
 * @desc 根据 router 路径获取对应的 pathname
 * @param url
 * @returns {*}
 */
export function getPathnameByUrl (url) {
  let module = store.getters['menu/menuUrlToModule'](url)
  let base = rootDirectory.replace(moduleName, module)
  return base + url
}

/**
 * @desc 跳转页面 ，通过菜单 id
 * @param id 菜单 id
 */
export function urlPushById (id) {
  let url = store.getters['menu/menuIdToUrl'](id)
  urlPush(url)
}

/**
 * @desc 跳转页面 ，通过菜单 id
 * @param id 菜单 id
 */
export function urlPushByRouteName (routeName) {
  if (routeName === 'login') {
    router.push('Login')
    return
  }
  let url = store.getters['menu/menuRouteNameToUrl'](routeName)
  urlPush(url)
}

/**
 * @desc 进入主页
 */
export function urlPushToMain () {
  // 不按模块打包的情况下，可以先跳转再获取菜单
  router.push({name: 'main'})
  // 按模块打包的情况下，main 指的是当前模块的主页面，不是系统的主页面，只能提前获取菜单
  // urlPushById(store.state.menu.mainMenu)
}

/**
 * @desc 添加窗口关闭前事件
 * @param fn
 */
export function beforeunload (fn) {
  window.addEventListener('beforeunload', function (event) {
    if (typeof fn === 'function') {
      fn()
    }
  }, true)
}

/**
 * @desc 判断密码等级
 * @param {String} password 密码
 * @returns {number}  0~4
 * @constructor
 */
export function analyzePasswordSecurityLevel (password) {
  let securityLevelFlag = 0
  if (password.length < 6) {
    return 0
  } else {
    if (/[a-z]/.test(password)) {
      // lowercase
      securityLevelFlag++
    }
    if (/[A-Z]/.test(password)) {
      // uppercase
      securityLevelFlag++
    }
    if (/[0-9]/.test(password)) {
      // digital
      securityLevelFlag++
    }
    if (containSpecialChar(password)) {
      // specialcase
      securityLevelFlag++
    }
    return securityLevelFlag
  }
}

/**
 * @desc 判断是否含有特殊字符
 * @param {String} str 字符串
 * @returns {boolean}
 */
export function containSpecialChar (str) {
  // eslint-disable-next-line no-useless-escape
  let containSpecial = RegExp(/[(\ )(\~)(\!)(\@)(\#)(\$)(\%)(\^)(\&)(\*)(\()(\))(\-)(\_)(\+)(\=)(\[)(\])(\{)(\})(\|)(\\)(\;)(\:)(\')(\")(\,)(\.)(\/)(\<)(\>)(\?)(\)]+/)
  return (containSpecial.test(str))
}

/**
 * 返回uuid
 * @returns {string}
 */
export function getUUID () {
  let s = []
  let hexDigits = '0123456789abcdef'
  for (let i = 0; i < 36; i++) {
    s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1)
  }
  s[14] = '4' // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1) // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = '-'

  let uuid = s.join('')
  return uuid
}

/**
 * @desc 根据当前路径获取菜单 id
 * @returns {*}
 */
export function getMenuId (route) {
  route = !route ? router.currentRoute : route
  return store.getters['menu/menuUrlToId'](objC(route, 'path'))
}

/**
 * @@desc 根据当前菜单 id 获得 top 的 id
 * @param id 菜单 id
 * @returns {undefined|*|*}
 */
export function getMenuTopId (id) {
  if (!id) return

  let top = store.getters['menu/menuIdToTop'](id)
  if (top) return id

  let pid = store.getters['menu/menuIdToPid'](id)

  return getMenuTopId(pid)
}



