<!--公用组件：
 /**
  * @desc HeaderNotice
  * @module @/components
  * @desc 组件描述：头部消息提醒
  * @author zkhq
  * @date 2020/3/23 14:27
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <div>
    <el-popover
      placement="bottom-end"
      width="400"
      :visible-arrow="false"
      :popper-options="{offset: '100px,50px'}"
      popper-class="header-notice-popper"
      trigger="click">
      <el-tabs v-model="activeName">
        <el-tab-pane label="通知 (3)" name="first">通知</el-tab-pane>
        <el-tab-pane label="消息 (3)" name="second">消息</el-tab-pane>
        <el-tab-pane label="待办 (4)" name="third">待办</el-tab-pane>
      </el-tabs>
      <el-badge :value="12" class="header-notice-badge" slot="reference">
        <ant-icon icon-class="BellOutlined"></ant-icon>
      </el-badge>
<!--      <ant-icon icon-class="BellOutlined" style="font-size: 16px;" slot="reference"></ant-icon>-->
    </el-popover>
  </div>
</template>

<script>
// import
export default {
  name: 'HeaderNotice',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      activeName: 'second'
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
  }
}
</script>

<style scoped>
  .right-menu-item > span{
    font-size: 16px;
  }
  .el-badge.header-notice-badge {
    height: 1em;
    line-height: 1em;
    cursor: pointer;
  }
  .ant-icon{
    font-size: 16px;
    float: left;
    transform: translate(0px, -1px);
  }
</style>

<style>
  .el-popper.header-notice-popper {
    margin-top: 26px;
    /*margin-top: 4px;*/
    /*border: 0;*/
  }
</style>



