<!--公用组件：
 /**
  * @desc BaseImgShow
  * @module @/components
  * @desc 组件描述：图片展示
  * @author zkhq
  * @date 2020/3/11 9:55
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <div class="imgZoomAndDrag" v-show="show" @mousemove="moveImg" @mouseup="moveEnd">
    <img :src="src" @load="setBoundary" ref="img" :style="{height, width, maxHeight, maxWidth, zoom, left: left + 'px', right: right + 'px', top: top + 'px', bottom: bottom + 'px'}">
    <div class="imgCover" ref="cover" @mousedown="moveStart"></div>
    <div class="imgClose iconfont icon-shanchu1" @click="closeImg"></div>
  </div>
</template>

<script>
// import
export default {
  name: 'BaseImgShow',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 图片高度
      height: '',
      // 图片宽度
      width: '',
      // 图片最大高度
      maxHeight: '',
      // 图片最大宽度
      maxWidth: '',
      // 图片缩放倍数
      zoom: 1,
      // 是否正在移动图片
      isMove: false,
      // 移动图片的鼠标最开始的位置
      x: 0,
      y: 0,
      // 图片上下左右的定位
      leftBase: -2000,
      rightBase: -2000,
      topBase: -2000,
      bottomBase: -2000,
      left: -2000,
      right: -2000,
      top: -2000,
      bottom: -2000
    }
  },
  props: {
    // src: {
    //   default: '',
    //   type: String
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
    // 添加图片滚动缩放图片事件
    this.$refs.cover.addEventListener('mousewheel', (event) => {
      let zoom = this.zoom
      zoom += event.wheelDelta / 1200
      if (zoom > 0) {
        this.zoom = zoom
      }
    })
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {
    src () {
      // 清除边界
      this.clearBoundary()
    }
  },
  computed: {
    // 图片路径
    src () {
      return this.$store.state.imageShow.src
    },
    // 图片路径不能是错误替代图片
    show () {
      return this.src && !(/assets\/images\/imgError/).test(this.src)
    }
  },
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 在图片加载完成后，设置图片当前的宽高为图片的默认宽高
     */
    setBoundary () {
      this.height = this.$common.notUndefined(this.$refs.img.offsetHeight) + 'px'
      this.width = this.$common.notUndefined(this.$refs.img.offsetWidth) + 'px'
      this.maxHeight = 'none'
      this.maxWidth = 'none'
    },
    /**
     * @desc 清除边界，避免图片加载时，影响默认宽高的设置
     */
    clearBoundary () {
      this.height = ''
      this.width = ''
      this.maxHeight = ''
      this.maxWidth = ''
      this.zoom = 1
      this.leftBase = this.left = -2000
      this.rightBase = this.right = -2000
      this.topBase = this.top = -2000
      this.bottomBase = this.bottom = -2000
    },
    /**
     * @desc 关闭图片
     */
    closeImg () {
      this.$store.commit('imageShow/imageShow', {
        src: ''
      })
    },
    /**
     * @desc 开始移动
     * @param e 事件对象
     */
    moveStart (e) {
      this.x = e.pageX
      this.y = e.pageY
      this.leftBase = this.left
      this.rightBase = this.right
      this.topBase = this.top
      this.bottomBase = this.bottom
      this.isMove = true
    },
    /**
     * @desc 结束移动
     * @param e 事件对象
     */
    moveEnd (e) {
      this.isMove = false
    },
    /**
     * @desc 移动图片
     * @param e 事件对象
     */
    moveImg (e) {
      if (!this.isMove) return

      let x = e.pageX
      let y = e.pageY
      this.left = this.leftBase + (x - this.x) / this.zoom
      this.right = this.rightBase - (x - this.x) / this.zoom
      this.top = this.topBase + (y - this.y) / this.zoom
      this.bottom = this.bottomBase - (y - this.y) / this.zoom
    }
  }
}
</script>

<style scoped>
  .imgZoomAndDrag {
    position: fixed;
    z-index: 1001;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .imgZoomAndDrag img {
    position: absolute;
    top: -2000px;
    left: -2000px;
    right: -2000px;
    bottom: -2000px;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
  }

  .imgZoomAndDrag .imgCover {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    cursor: all-scroll;
  }

  .imgZoomAndDrag .imgClose {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    background-color: #ff0000;
    cursor: pointer;
  }

  .imgZoomAndDrag .imgClose.iconfont {
    position: absolute;
    top: 0;
    right: 0;
    width: 57px;
    height: 50px;
    line-height: 50px;
    cursor: pointer;
    font-size: 36px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.2);
  }
</style>



