import {objC} from '@/utils/common'

const state = {
  menuArr: [],
  menuTree: [],
  mainMenu: 2,
  mainModuleMenu: 2,
  menuIdToMenu: {},
  menuRouteNameToMenu: {},
  menuUrlToMenu: {}
}

const getters = {
  menuOptions (state) {
    let arr = []
    addMenuTree(state.menuTree, arr)
    return arr
  },
  menuUrlToId: (state) => (url) => objC(state.menuUrlToMenu, url, 'id') || '',
  menuUrlToIds: (state) => (url) => objC(state.menuUrlToMenu, url, 'ids') || [],
  menuUrlToModule: (state) => (url) => objC(state.menuUrlToMenu, url, 'module') || [],
  menuIdToUrl: (state) => (id) => objC(state.menuIdToMenu, id, 'url') || '',
  menuIdToPid: (state) => (id) => objC(state.menuIdToMenu, id, 'pid') || '',
  menuIdToTop: (state) => (id) => objC(state.menuIdToMenu, id, 'top') || false,
  menuRouteNameToUrl: (state) => (routeName) => objC(state.menuRouteNameToMenu, routeName, 'url') || ''
}

const mutations = {
  menuArrSave (state, obj) {
    for (let i = 0, l = obj.arr.length; i < l; i++) {
      let item = obj.arr[i]
      if (state.menuIdToMenu[item.id]) {
        continue
      }
      state.menuArr.push(item)
      state.menuIdToMenu[item.id] = item
      state.menuRouteNameToMenu[item.routeName] = item
      state.menuUrlToMenu[item.url] = item
    }
  },
  menuTreeSave (state, obj) {
    state.menuTree = obj.arr
  },
  mainModuleMenuSave (state, obj) {
    state.mainModuleMenu = obj.num
  },
  mainMenuSave (state, obj) {
    state.mainMenu = obj.num
  }
}

// 生成树结构菜单
function addMenuTree (menuArr, arr = []) {
  for (let i = 0, l = objC(menuArr, 'length'); i < l; i++) {
    let menu = menuArr[i]
    let item = {value: menu.id, label: menu.name}
    if (menu.module === 'base') {
      continue
    }
    arr.push(item)
    if (objC(menu, 'data', 'length')) {
      item.children = []
      addMenuTree(menu.data, item.children)
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations
}



