import {isArray, objSet, objC} from '@/utils/common'

const state = {
  arr: [],
  idToButton: {},
  componentToButton: {},
  menuToButtonArr: {},
  menuToButtonDict: {}
}

const getters = {
  hasButtom: (state) => ({menu, component}) => {
    return objC(state.menuToButtonDict, menu, objC(state.componentToButton[component], 'id'))
  }
}

const mutations = {
  /**
   * @desc 保存按钮数据
   * @param state vuex 的数据
   * @param arr 需要保存的数据
   */
  arrSave (state, {arr}) {
    for (let i = 0, l = arr.length; i < l; i++) {
      let item = arr[i]
      if (state.idToButton[item.id]) {
        continue
      }
      state.arr.push(item)
      state.idToButton[item.id] = item
      state.componentToButton[item.component] = item
      if (!isArray(state.menuToButtonArr[item.pid])) {
        state.menuToButtonArr[item.pid] = []
      }
      state.menuToButtonArr[item.pid].push(item.id)
      objSet(true, state.menuToButtonDict, item.pid, item.id)
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations
}



