const state = {
  // 污染等级
  arr: [
    'l1',
    'l2',
    'l3',
    'l4',
    'l5',
    'l6'
  ],
  // 污染等级 =》 显示
  levelToShow: {
    l1: '优',
    l2: '良',
    l3: '轻度污染',
    l4: '中度污染',
    l5: '重度污染',
    l6: '严重污染'
  },
  // 污染等级 =》 简写显示
  levelToSimpleShow: {
    l1: '优',
    l2: '良',
    l3: '轻',
    l4: '中',
    l5: '重',
    l6: '严'
  },
  // 污染等级 =》 颜色
  levelToColors: {
    l1: '#43ce17',
    l2: '#efdc31',
    l3: '#ffaa00',
    l4: '#ff401a',
    l5: '#d20040',
    l6: '#9c0a4e'
  },
  // 污染等级 =》 字体颜色
  levelToFontColors: {
    l1: '#282828',
    l2: '#282828',
    l3: '#ffffff',
    l4: '#ffffff',
    l5: '#ffffff',
    l6: '#ffffff'
  },
  // 污染物 =》 污染等级
  pollToLvlLine: {
    so2: [150, 500, 650, 800],
    no2: [100, 200, 700, 1200, 2340, 3090, 3840],
    pm10: [50, 150, 250, 350, 420, 500, 600],
    pm25: [35, 75, 115, 150, 250, 350, 500],
    o3: [160, 200, 300, 400, 800, 1000, 1200],
    co: [5, 10, 35, 60, 90, 120, 150],
    aqi: [50, 100, 150, 200, 300, 500],
    tvoc: []
  }
}

const getters = {
  // /**
  //  * @desc 通过污染等级获取污染颜色
  //  * @param {String} lvl 污染等级
  //  * @returns {String} 污染颜色
  //  */
  getColorByLevel: state => lvl => state.levelToColors[lvl],
  // /**
  //  * @desc 通过污染等级获取污染颜色对应的字体颜色
  //  * @param {String} lvl 污染等级
  //  * @returns {String} 污染颜色对应的字体颜色
  //  */
  getFontColorByLevel: state => lvl => state.levelToFontColors[lvl],
  // /**
  //  * @desc 通过污染等级获取显示
  //  * @param {String} lvl 污染等级
  //  * @returns {String} 显示
  //  */
  getShowByLevel: state => lvl => state.levelToShow[lvl],
  // /**
  //  * @desc 通过污染等级获取简写显示
  //  * @param {String} lvl 污染等级
  //  * @returns {String} 简写显示
  //  */
  getSimpleShowByLevel: state => lvl => state.levelToSimpleShow[lvl],
  // /**
  //  * @desc 通过污染物获取最高污染值
  //  * @param {String} poll 污染物
  //  * @returns {String} 最高污染值
  //  */
  getMaxValueByPoll: state => poll => {
    let l = state.pollToLvlLine[poll].length
    if (l) {
      return state.pollToLvlLine[poll][l - 1]
    } else {
      return 0
    }
  },
  // /**
  //  * @desc 通过污染物和污染值获取污染级别
  //  * @param {Number} num 污染值
  //  * @param {String} poll 污染物
  //  * @returns {String} 污染级别
  //  */
  getLevelByNumber: state => ({num, poll} = {}) => {
    num = parseFloat(num)
    if (!num && num !== 0) {
      return '-'
    }
    if (!poll || !(state.pollToLvlLine[poll] && state.pollToLvlLine[poll].length)) {
      return '-'
    }
    let l = state.arr.length - 1
    for (let i = 0, l2 = state.pollToLvlLine[poll].length; i < l2; i++) {
      if (num <= state.pollToLvlLine[poll][i]) {
        return i > l ? state.arr[l] : state.arr[i]
      }
    }
    return state.arr[l]
  }
}

export default {
  namespaced: true,
  state,
  getters
}



