import Vue from 'vue'
import Highcharts from 'highcharts'
import HighchartsVue from 'highcharts-vue'
import exportingInit from 'highcharts/modules/exporting'
import exportDataInit from 'highcharts/modules/export-data'
import offlineExportingInit from 'highcharts/modules/offline-exporting'
import highchartsMoreInit from 'highcharts/highcharts-more'
import windbarbInit from 'highcharts/modules/windbarb'
import oldieInit from 'highcharts/modules/oldie'
import drillDownInit from 'highcharts/modules/drilldown'
import {notUndefined} from '@/utils/common'
exportingInit(Highcharts)
exportDataInit(Highcharts)
offlineExportingInit(Highcharts)
highchartsMoreInit(Highcharts)
windbarbInit(Highcharts)
oldieInit(Highcharts)
drillDownInit(Highcharts)

Vue.use(HighchartsVue)
Vue.use(Highcharts)

Highcharts.setOptions({
  chart: {
    backgroundColor: 'rgba(0,0,0,0)', // 透明背景颜色
    plotBorderWidth: 1,
    plotBorderColor: '#dedfe2'
  },
  exporting: {
    filename: '下载文件',
    allowHTML: false,
    scale: 1,
    chartOptions: {
      credits: {
        enabled: false // 下载图时添加版权信息
      },
      chart: {
        backgroundColor: '#FFFFFF'
      }
    },
    buttons: {
      contextButton: {
        symbolStroke: '#2077ff',
        symbolStrokeWidth: 2,
        menuItems: [
          {
            text: '下载 PNG 文件',
            onclick: function () {
              this.exportChartLocal({
                sourceHeight: this.chartHeight,
                sourceWidth: this.chartWidth,
                type: 'application/png'
              })
            }
          }, {
            text: '下载 JPEG 文件',
            onclick: function () {
              this.exportChartLocal({
                sourceHeight: this.chartHeight,
                sourceWidth: this.chartWidth,
                type: 'application/jpeg'
              })
            }
          }, {
            text: '下载 CSV 文件',
            onclick: function () {
              this.downloadCSV()
            }
          }, {
            text: '下载 XLS 文件',
            onclick: function () {
              this.downloadXLS()
            }
          }
        ]
      }
    }
  },
  navigation: {
    menuItemStyle: {
      color: '#7d8097',
      padding: '0 10px',
      lineHeight: '30px',
      width: '120px'
    },
    menuItemHoverStyle: {
      background: '#2077ff',
      color: '#ffffff'
    },
    menuStyle: {
      boxShadow: 'rgb(32, 119, 255, 0.4) 0px 0px 6px',
      border: 0,
      padding: '0px'
    }
  },
  global: {
    timezoneOffset: -8 * 60 // +8时区修正方法
  },
  credits: {
    enabled: false, // 禁用版权信息
    text: '中科弘清',
    href: 'javascript:void(0);',
    style: {
      fontSize: '12px'
    }
  },
  title: {
    style: {
      color: '#373737',
      fontSize: '14px',
      fontWeight: 'bold' // 黑体
    },
    y: 15,
    useHTML: true
  },
  legend: {
    useHTML: true,
    align: 'center',
    verticalAlign: 'top',
    itemStyle: {
      fontSize: '14px',
      color: '#373737',
      fontWeight: 'normal'
    }
  },
  xAxis: {
    lineColor: '#dedfe2', // 轴线颜色
    tickColor: '#dedfe2', // 刻度颜色
    lineWidth: 0,
    type: 'datetime',
    crosshair: true, // 十字准星
    style: {
      fontSize: '14px',
      letterSpacing: 0,
      color: '#373737'
    },
    labels: {
      style: {
        color: '#373737',
        fontSize: '14px',
        fontWeight: 'normal' // 黑体
      },
      formatter: function () {
        return '' + Highcharts.dateFormat('%m/%d', this.value) + '' + '<br/>' + Highcharts.dateFormat('%H:%M', this.value)
      }
    }
  },
  yAxis: {
    lineColor: '#dedfe2', // 轴线颜色
    tickColor: '#dedfe2', // 刻度颜色
    gridLineColor: '#dedfe2', // 网格线颜色
    tickWidth: 1,
    title: {
      text: '',
      useHTML: true,
      style: {
        color: '#373737',
        fontSize: '14px',
        fontWeight: 'bold' // 黑体
      }
    },
    labels: {
      style: {
        color: '#373737',
        fontSize: '14px',
        fontWeight: 'normal' // 黑体
      }
    }
  },
  tooltip: {
    shared: true,
    useHTML: true,
    formatter: function () {
      if (this.points != null) {
        let result = '<span style="font-size: 10px;">' + Highcharts.dateFormat('%Y-%m-%d %H:%M', this.x) + '</span><br/>'
        this.points.forEach(point => {
          result += '<span style="color: ' + point.series.color + '">' + point.series.name + '</span>: <b>' + point.y.toFixed(3) + '</b> ' + notUndefined(point.series.userOptions.unit) + '<br/>'
        })
        return result
      }
    }
  },
  plotOptions: {
    column: {
      stacking: 'normal',
      dataLabels: {
        enabled: false
      },
      pointPadding: -0.1
    },
    spline: {
      marker: {
        enabled: false
      },
      lineWidth: 3
    },
    pie: {
      allowPointSelect: false,
      animation: false,
      showInLegend: false,
      cursor: 'pointer',
      dataLabels: {
        enabled: true,
        distance: 5, // 饼图标签距离饼的连接线距离，为负数时表示可以显示在饼上
        padding: 0,
        format: '<b >{point.name}</b>: <br/><span >{point.percentage:.1f} %</span>',
        style: {
          fontSize: '10px'
        },
        useHTML: true
      }
    },
    line: {
    },
    series: {
      turboThreshold: 0
    }
  }
})



